  text-align: center;
  text-decoration: none;
  color: #ffffff;
  background-color: #7859cf;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
}
.button:hover:not(.button--disabled):not(:disabled) {
  background-color: #9070ec;
}
.button:focus-visible:not(.button--disabled):not(:disabled) {
  background-color: #9070ec;
  outline: none;
}
.button:active:not(.button--disabled):not(:disabled) {
  background-color: #593cac;
  outline: none;
}
.button:disabled {
  background-color: #bdbdbd;
  cursor: default;
}
.button--disabled {
  background-color: #bdbdbd;
  cursor: default;
}

.button--light {
