.navigation {
  position: relative;
  display: grid;
  grid-template-areas: "nav-logo nav-user nav-toggle" "nav-site nav-site nav-site  ";
  grid-template-columns: 72px 1fr 60px;
  align-items: center;
}

.navigation__logo {
  grid-area: nav-logo;
}

.navigation__site {
  grid-area: nav-site;
  position: absolute;
  top: 0;
  left: 0;
}

.navigation__user {
  grid-area: nav-user;
}

.navigation__toggle {
  display: block;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 1.4285714286;
  font-weight: 400;
  text-decoration: none;
  color: #333333;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  cursor: pointer;
  grid-area: nav-toggle;
  position: relative;
  width: 60px;
  height: 60px;
  padding: 24px 22px;
}
.navigation__toggle:active {
  background-color: #f0ebff;
  outline: none;
}

@media (max-width: 767px) {
  .navigation--closed .navigation__site {
    display: none;
  }

  .navigation--closed .navigation__toggle::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: "";
    width: 16px;
    height: 1.5px;
    margin: auto;
    background-color: #333333;
    box-shadow: 0 5px 0 0 #333333, 0 -5px 0 0 #333333;
  }

  .navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: "";
    width: 16px;
    height: 2px;
    margin: auto;
    background-color: #333333;
  }
  .navigation--opened .navigation__toggle::before {
    transform: rotate(45deg);
  }
  .navigation--opened .navigation__toggle::after {
    transform: rotate(-45deg);
  }

  .js-exception .navigation {
    grid-template-columns: 72px 1fr;
  }

  .js-exception .navigation__site {
    position: static;
    display: block;
  }

  .js-exception .navigation__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation {
    grid-template-areas: "nav-logo nav-site nav-user";
    grid-template-columns: 90px 1fr auto;
  }

  .navigation__site {
    position: static;
  }

  .navigation__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .navigation {
    grid-template-columns: 336px 1fr auto;
  }
}
