.slider__toggle {
  display: block;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 1.4285714286;
  font-weight: 400;
  text-decoration: none;
  color: #333333;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  cursor: pointer;
  z-index: 1;
  position: absolute;
  top: 0;
  display: flex;
  width: 20px;
  height: 100%;
