}
.slider__toggle--next {
  right: 0;
  background-color: rgba(0, 0, 0, 0);
}
.slider__toggle--previous:hover {
  background-image: linear-gradient(90deg, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
}
.slider__toggle--next:hover {
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #ffffff 100%);
}
.slider__toggle--previous:focus-visible {
  background-image: linear-gradient(90deg, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
  outline: none;
}
.slider__toggle--next:focus-visible {
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #ffffff 100%);
  outline: none;
}
.slider__toggle--previous:active {
  background-image: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}
.slider__toggle--next:active {
  background-image: linear-gradient(90deg, rgba(120, 89, 207, 0) 0%, rgba(120, 89, 207, 0.25) 100%);
}
.slider__toggle--disabled {
  color: #bdbdbd;
  background-color: rgba(0, 0, 0, 0);
  background-image: none;
  cursor: default;
}
.slider__toggle:disabled {
  color: #bdbdbd;
  background-color: rgba(0, 0, 0, 0);
  background-image: none;
  cursor: default;
}

.slider__toggle-icon {
  margin: auto;
}
.slider__toggle-icon--previous {
  transform: rotate(180deg);
}

.slider__pagination {
