}
@media (min-width: 1440px) {
  .slider__toggle {
    height: 100%;
  }
  .slider__toggle--previous {
    left: calc((100vw - 1440px) / 2);
  }
  .slider__toggle--next {
    right: calc((100vw - 1440px) / 2);
  }

  .slider__pagination {
